.calculator {
  margin-top: 1rem;
  float: right;
  margin-right: 10%;
  padding-bottom: 2rem;
}

.do-math {
  margin-top: 4rem;
  font-family: 'Orbitron', sans-serif;
}

.navBar {
  padding: 2rem;
  background-color: gray;
  box-shadow: rgba(50, 50, 93, 0.25) 0 30px 60px -12px, rgba(0, 0, 0, 0.3) 0 18px 36px -18px;
}

.navBar ul {
  list-style: none;
  display: flex;
  justify-content: right;
  justify-items: right;
  margin-right: 5rem;
  margin-top: -3rem;
  gap: 1rem;
}

.navBar ul li a {
  text-decoration: none;
  font-family: 'Orbitron', sans-serif;
  color: white;
}

.navBar h1 {
  font-family: 'Orbitron', sans-serif;
  color: orangered;
}

.navBar ul li .active-link {
  color: orangered;
  text-decoration: underline;
}

.welcome {
  font-family: 'Orbitron', sans-serif;
  font-weight: bolder;
  font-size: 4rem;
  padding: 1rem;
}

.welcome:hover {
  color: orangered;
}

.intro {
  font-family: monospace;
  margin: 1rem;
  padding: 1rem;
  font-weight: 800;
  font-size: 1rem;
  border-left: 5px solid orangered;
  background-color: rgb(228, 228, 228);
}

table {
  width: 100%;
  border: 1px solid black;
  border-collapse: collapse;
  box-shadow: rgba(0, 0, 0, 0.4) 0 2px 4px, rgba(0, 0, 0, 0.3) 0 7px 13px -3px, rgba(0, 0, 0, 0.2) 0 -3px 0 inset;
}

table th {
  padding: 0;
  margin: 0;
  border: 2px solid gray;
}

.screen {
  display: flex;
}

.input-operation {
  font-family: 'Orbitron', sans-serif;
  padding: 1rem 2rem;
  text-align: center;
  background-color: #bccd95;
  box-shadow: rgba(0, 0, 0, 0.06) 0 2px 4px 0 inset;
  color: black;
  font-weight: bold;
  font-size: 2rem;
  border: 3px solid gray;
}

.input-number {
  font-family: 'Orbitron', sans-serif;
  padding: 3rem 1.6rem;
  background-color: #bccd95;
  box-shadow: rgba(0, 0, 0, 0.06) 0 2px 4px 0 inset;
  color: black;
  text-align: right;
  font-weight: bold;
  letter-spacing: 0.2rem;
  font-size: 2rem;
  border: 3px solid gray;
}

.zero {
  padding: 2.5rem;
  margin-right: 5rem;
  border: none;
  font-weight: bolder;
  font-family: 'Orbitron', sans-serif;
  font-size: 1rem;
}

.numbers {
  font-family: 'Orbitron', sans-serif;
  font-size: 1rem;
  display: grid;
  color: black;
  border: none;
  padding: 2.5rem 4rem;
  font-weight: bolder;
}

.glob {
  width: 100%;
  cursor: pointer;
}

.glob:active {
  transition: 100ms;
  box-shadow: rgba(50, 50, 93, 0.25) 0 30px 60px -12px inset, rgba(0, 0, 0, 0.3) 0 18px 36px -18px inset;
}

.operations {
  background-color: orange;
  font-family: 'Orbitron', sans-serif;
  font-size: 1rem;
  border: none;
  padding: 2.5rem 4rem;
  margin: 0;
  font-weight: bolder;
}

.quote-author {
  margin-top: 10rem;
  margin-left: 10rem;
  font-family: monospace;
  font-size: 2rem;
}

.quote {
  font-family: monospace;
  font-weight: bolder;
  text-align: center;
  font-size: 2rem;
  padding: 2rem 1rem;
  background-color: rgb(228, 228, 228);
  margin: 1rem 1rem;
  border: 5px solid orangered;
  box-shadow: rgba(100, 100, 111, 0.2) 0 7px 29px 0;
}
